:root {
  --font-kumbh: "Kumbh Sans", sans-serif;
}

:root {
  --orange: hsl(26, 100%, 55%);
  --pale-orange: hsl(25, 100%, 94%);
  --very-dark-blue: hsl(220, 13%, 13%);
  --dark-grayish-blue: hsl(219, 9%, 45%);
  --grayish-blue: hsl(220, 14%, 75%);
  --light-grayish-blue: hsl(223, 64%, 98%);
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
}

.container {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 2.1875rem;
  border-bottom: 0.09375rem solid var(--grayish-blue);
  padding: 1.25rem 0 2.3125rem 0;
  margin: 0.625rem auto 2.5rem auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (max-width: 23.4375em) {
  .container {
    height: 0rem;
    margin-bottom: 0;
    margin-top: 0.9375rem;
    display: flex;
    align-items: center;
    justify-content: space-around;
  }
}

body {
  margin: 0;
  padding: 0 6.875rem;
  box-sizing: border-box;
  font-family: var(--font-kumbh);
  background: var(--white);
  height: 100vh;
}

h1 {
  font-size: 2.8125rem;
  font-weight: 700;
}

body {
  position: relative;
}
@media (max-width: 23.4375em) {
  body {
