}

.main__section {
  z-index: 1;
}
.main__nav {
  position: fixed;
  z-index: 3;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.7);
  opacity: 0;
  visibility: hidden;
  transition: 0.5s ease;
}
@media (max-width: 23.4375em) {
  .main__nav {
    display: none;
  }
}
.main__nav--cont {
  display: block;
}
.main__nav--cont__close {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  top: 0.75rem;
  left: 56.1875rem;
  color: var(--white);
  cursor: pointer;
  transition: 0.3s ease-in-out;
}
.main__nav--cont__close:hover {
  color: #ff8324;
}
.main__nav--cont__whole {
  display: flex;
  align-items: center;
  justify-content: center;
}
.main__nav--cont__whole img {
  position: relative;
  z-index: 3;
  width: 29.375rem;
  border-radius: 0.9375rem;
  top: 1.25rem;
  bottom: auto;
}
.main__nav--cont__whole svg {
  position: relative;
  z-index: 4;
  background: var(--light-grayish-blue);
  padding: 1rem 1.1875rem;
  border-radius: 1.875rem;
  top: 0.75rem;
  font-size: 1.875rem;
  color: var(--very-dark-blue);
  cursor: pointer;
  transition: 0.3s ease-in-out;
}
.main__nav--cont__whole svg:hover {
  color: #ff8b33;
}
.main__nav--cont__whole__previous {
  left: 1.5rem;
}
.main__nav--cont__whole__next {
  right: 1.5rem;
}
.main__nav--cont__additional {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1.5625rem;
  justify-content: center;
  align-items: center;
  width: 23.125rem;
}
.main__nav--cont__additional--thumbnail {
  background: var(--white);
  position: relative;
  top: 1.5625rem;
  left: 30.875rem;
  height: 4.375rem;
  border: 0.125rem solid transparent;
  border-radius: 0.75rem;
  overflow: hidden;
  cursor: pointer;
  transition: 0.3s ease-in-out;
}
