}
.main__nav--cont__additional--thumbnail.selected img {
  opacity: 25%;
}
.main__nav--cont__additional--thumbnail.selected:hover img {
  opacity: 25%;
}
.main__nav--cont__additional--thumbnail:hover img {
  opacity: 50%;
}
.main__nav--cont__additional--thumbnail img {
  position: relative;
  width: 100%;
}

